.decor {
  position: relative;
  background-color: #F4F8FD;
}

.decor::after {
  position: absolute;
  z-index: 1;
  right: 0;
  bottom: 0;
  content: '';
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 0 12px 12px;
  border-color: transparent transparent #fff transparent;
}

.decor__line {
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 92%;
  height: 1px;
  background-color: rgba(218, 229, 236, 0.65);
  align-self: flex-end;
}

.decor__line::before, .decor__line::after {
  position: absolute;
  z-index: 1;
  bottom: 0;
  content: '';
  transition: border-color .3s;
}

.decor__line::before {
  right: 1px;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 0 12px 10px;
  border-color: transparent transparent rgba(0, 0, 0, 0.25) transparent;
}

.decor__line::after {
  right: -12px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 12px 13px 0 0;
  border-color: rgba(238, 244, 251, 0.9) transparent transparent transparent;
}

.decor__line_suggest {
  width: 95%;
}

.decor_sm {
  background-color: #D7DCE0;
}

.decor_sm::after {
  display: none;
}

.decor_sm .decor__line {
  width: 85%;
}

.decor_sm .decor__line:after {
  right: -20px;
  border-width: 19px 19px 0 0;
  background-color: #ececec;
}

.decor_sm .decor__line:before {
  right: -1px;
}

.decor_xs::after {
  border-width: 0 0 20px 19px;
  right: -2px;
}

.decor_xs .decor__line {
  width: 80%;
  bottom: 0;
}

.decor_xs .decor__line::after {
  right: -26px;
  border-width: 18px 18px 0 0;
}

.decor_xs .decor__line::before {
  right: -8px;
  border-width: 0 0 18px 13px;
}

/* page
	----------------------------------- */
.page_default {
  padding: 35px 0 0;
}

.page__bar {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
  background: #EBECEC;
}

.page__bar-title {
  padding: 10px 30px;
  margin-left: auto;
  background: #F3F3F3;
  border-radius: 50px 0 0 50px;
  min-width: 250px;
  font-size: 23px;
  text-align: center;
  color: #333333;
}

.page__in {
  display: flex;
  justify-content: space-between;
  margin: 30px 0 0;
}

.page__side {
  width: 18.45238%;
}

.page__main {
  display: flex;
  flex-direction: column;
  width: 79.7619%;
}

/* bordered decor
	----------------------------------- */
.bordered {
  position: relative;
}

.bordered::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 5px;
  right: 5px;
  height: 1px;
  background: #D1D1D1;
  z-index: 1;
}

/* sub-menu
	----------------------------------- */
.category-menu {
  display: flex;
}

.category-menu__item {
  flex: 1 0 auto;
}

.category-menu__link {
  display: block;
  padding: 18px 5px;
  font-size: 20px;
  background: #F7FAFF;
  text-align: center;
  transition: 0.2s cubic-bezier(0.55, 0.09, 0.68, 0.53);
}

.category-menu__link:hover {
  background: #E5F0FC;
}

.category-menu_default {
  margin-top: auto;
}

/* more-text-btn
	----------------------------------- */
.more-text {
  color: #004D87;
  font-size: 17px;
}

/* links
	----------------------------------- */
.link-sale {
  color: #FCBD7E;
  transition: 0.2s cubic-bezier(0.55, 0.09, 0.68, 0.53);
}

.link-sale span {
  border-bottom: 2px dashed #FCBD7E;
}

.link-sale:hover span {
  color: #fa9733;
  border-bottom-color: #fa9733;
}

/* rating
	----------------------------------- */
.rating__text {
  margin-bottom: 3px;
  color: #6D89A7;
  font-size: 16px;
}

.rating__in {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.rating__input {
  display: none;
}

.rating__input ~ .rating__icon .rating__star {
  fill: #DBDBDB;
  stroke: #DBDBDB;
}

.rating__input:checked ~ .rating-icon .rating__star,
.rating__input:checked .rating-icon .rating__star {
  fill: #FFED00;
  stroke: #FFED00;
}

.rating__icon:hover .rating__star,
.rating__icon:hover ~ .rating__icon .rating__star {
  fill: #C7CBCF;
  stroke: #DBDBDB;
}

.rating__input:checked .rating__icon:hover .rating__star,
.rating__input:checked ~ .rating__icon .rating__star {
  fill: #FFED00;
  stroke: #FFED00;
}

.rating__icon {
  display: inline-block;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.rating__icon:not(:last-child) {
  margin-left: 2px;
}

.rating__star {
  font-size: 22px;
}

/* pagination
	----------------------------------- */
.pagination {
  font-size: 0;
}

.pagination__list {
  font-size: 0;
  text-align: center;
}

.pagination__list-item {
  display: inline-block;
  vertical-align: middle;
  margin: 0 2px;
}

.pagination__list-item_prev {
  margin-right: 10px;
  text-align: center;
}

.pagination__list-item_prev .pagination__list-link {
  padding: 0;
  line-height: 39px;
}

@media only screen and (max-width: 1599px) {
  .pagination__list-item_prev .pagination__list-link {
    line-height: 34px;
  }
}

@media only screen and (max-width: 424px) {
  .pagination__list-item_prev .pagination__list-link {
    line-height: 29px;
  }
}

.pagination__list-item_dots {
  margin: 0 2px;
}

.pagination__list-item_next .pagination__list-link {
  color: #768EA7;
}

.pagination__list-item.is-active .pagination__list-link {
  background: #FFEBAB;
}

.pagination__list-link {
  display: inline-block;
  font-family: Arial, sans-serif;
  color: #125082;
  border-radius: 5px;
  padding: 4px;
  height: 30px;
  line-height: 24px;
  min-width: 31px;
  font-size: 18px;
}

.pagination__list-link:hover {
  background: #EDEDED;
}

.pagination__list-link:active {
  background: #C8D6E6;
}

.pagination__list-link:hover .pagination__icon-arr {
  fill: #a6b5cc;
}

.pagination__icon-arr {
  display: inline-block;
  vertical-align: middle;
  fill: #EDEDED;
  font-size: 20px;
}

.pagination_product {
  padding: 30px 0;
  background: #fff;
  border: 5px solid #ebecec;
  border-top: 0;
}

.pagination_watched {
  padding: 20px 0 25px;
  background: #ebf3fc;
}

.pagination_watched .pagination__icon-arr {
  fill: #B8C6DB;
}

/* amount
	----------------------------------- */
.amount {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0 0 15px;
  background: #f4f8fd;
}

.amount__text {
  font-size: 20px;
  color: #00306B;
}

.amount__count {
  padding: 8px 10px 8px 25px;
  background-color: #eef5fc;
  border-radius: 30px 0 0 30px;
}

.amount__input {
  margin-right: 15px;
  padding-right: 5px;
  font-size: 21px;
  color: #000;
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: 600;
  width: 52px;
  height: 29px;
  line-height: 29px;
  text-align: right;
}
